.display {
    width: 45vh;
    height: 34vh;
    display: flex;
    flex-wrap: wrap;
    text-align: right;
    align-content: flex-end;
    color: #ffff;
  }
  .mode {
    height: 10vh;
    width: 45vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
  }
  .containerBtn {
    height: fit-content;
    background-color: #292d36;
    border: 0.3vh solid transparent;
    border-radius: 1.5vh;
  }
  .modeBtn {
    border: none;
    transition: box-shadow 0.1s ease-in-out;
    background-color: #292d36;
    width: 4vh;
    height: 4vh;
    padding: 0;
    color: #b9babc;
    cursor: pointer;
    font-size: 2.5vh;
    border: 0.3vh solid transparent;
  }
  .modeBtn:active {
    box-shadow: 0 0 0 0.3vh rgba(0, 123, 255, 0.25);
  }
  .modeBtn:focus {
    color: white;
  }
  .modeLight {
    border-radius: 1.5vh 0vh 0vh 1.5vh;
    margin-right: 0.4vh;
  }
  .modeDark {
    border-radius: 0vh 1.5vh 1.5vh 0vh;
  }
  
  .operation {
    width: 45vh;
    height: 8vh;
    padding-right: 4vh;
    font-size: 4vh;
    color: #b9babc;
  }
  .operador {
    color: #d25f5f;
  }
  .result {
    width: 45vh;
    height: 12vh;
    padding-right: 4vh;
    font-size: 8vh;
  }